const testPage = () => import("./test-page.vue");
const ck = () => import("./ck/ck.vue");
const cjx = () => import("./cjx/cjx.vue");
const zmn = () => import("./zmn/zmn.vue");

const route = [
    {
        path: "/test-page",
        name: "test-page",
        component: testPage,
        meta: {
            requireAuth: true //
        },
        redirect:{name:'test-home'},
        children: [
            {
                path: "home",
                name: "test-home",
                component: {render:h=>h("div","")},
                meta: {
                    requireAuth: true, //
                    abandonMenu:true//menu
                }
            },
            {
                path: "ck",
                name: "ck",
                component: ck,
                meta: {
                    requireAuth: true //
                }
            },
            {
                path: "cjx",
                name: "cjx",
                component: cjx,
                meta: {
                    requireAuth: true //
                },
            },
            {
                path: "zmn",
                name: "zmn",
                component: zmn,
                meta: {
                    requireAuth: true //
                }
            },
        ]
    }
];
export default (NODE_ENV === "production" ? [] : route);
